<template>
  <div class="order">
    <div v-if="isLogin">
      <van-tabs
        :sticky="true"
        v-model="activeName"
        @change="selectOrderStatus"
        color="rgb(215, 180, 148)"
        line-height="2"
        title-active-color="brown"
      >
        <van-tab
          :title="item.title"
          :name="item.name"
          v-for="(item,index) in tabContent"
          :key="index"
        >
          <div class="order-content">
            <div class="order-items">
              <div class="order-item" v-for="(item,index) in orderNum" :key="index">
                <div class="order-item-title">
                  <div class="fl order-no">{{item}}</div>
                  <div class="fr" v-if="orderData[item].status == 2">
                    <span class="confirm fl">已收货</span>
                    <div class="delete fl" @click="removeOrder(item)">
                      <van-icon class="delete-icon" name="delete" />
                    </div>
                  </div>
                  <div class="fr" v-else>
                    <span class="confirm" @click="receive(item)">确认收货</span>
                  </div>
                </div>
                <!-- 订单商品 -->
                <div>
                  <div class="items" v-for="(v,i) in orderData[item].data" :key="i">
                    <div class="clearfix">
                      <div class="fl pro-img">
                        <img class="auto-img" :src="v.smallImg" alt />
                      </div>
                      <div class="fl pro-info">
                        <div class="fl">
                          <div class="names">
                            <div class="zh-name">{{v.name}}</div>
                            <div class="en-name">{{v.enname}}</div>
                          </div>
                          <div class="rule">{{v.rule}}</div>
                        </div>
                        <div class="fr">
                          <div class="price">￥{{v.price}}</div>
                          <div class="count">x{{v.count}}</div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="order-total clearfix">
                  <div
                    class="order-data fl"
                  >{{orderData[item].date | formatDate('yyyy-MM-dd hh:mm:ss')}}</div>
                  <div class="fr clearfix">
                    <span class="fl total-count">共计 {{orderData[item].count}} 件商品 合计：</span>
                    <span class="fl total-price">￥{{orderData[item].total}}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
    <div v-else>
      <van-empty description="没有订单数据" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isLogin: false,
      tabContent: [
        {
          title: "全部",
          name: "0"
        },
        {
          title: "待收货",
          name: "1"
        },
        {
          title: "已收货",
          name: "2"
        }
      ],
      activeName: "0",
      isreceive: false,
      //订单编号
      orderNum: [],
      //订单数据
      orderData: {}
    };
  },
  created() {
    if (localStorage.getItem("__TK")) {
      this.isLogin = true;
      //获取订单商品
      this.getOrderProduct();
    }
  },
  methods: {
    //切换不同状态的商品
    selectOrderStatus(a) {
      this.getOrderProduct();
    },
    //获取订单商品
    getOrderProduct() {
      //获取token
      let tokenString = localStorage.getItem("__TK");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findOrder",
        params: {
          appkey: this.appkey,
          tokenString,
          status: this.activeName
        }
      })
        .then(result => {
          //关闭加载提示
          this.$toast.clear();

          if (result.data.code == 70000) {
            //降序排序
            result.data.result.sort((a, b) => {
              return (
                new Date(b.createdAt).getTime() -
                new Date(a.createdAt).getTime()
              );
            });
            //获取所有订单编号
            let oids = [];
            result.data.result.forEach(v => {
              if (oids.indexOf(v.oid) === -1) {
                oids.push(v.oid);
              }
            });
            this.orderNum = oids;
            //按照订单编号筛选商品
            let orderData = {};
            result.data.result.forEach(v => {
              if (!orderData[v.oid]) {
                //如果当前订单数据不存在，则添加一个新订单
                orderData[v.oid] = {
                  data: [v],
                  status: v.status,
                  date: v.createdAt,
                  count: v.count,
                  total: v.count * v.price
                };
              } else {
                orderData[v.oid].count += v.count;
                orderData[v.oid].total += v.count * v.price;
                orderData[v.oid].data.push(v);
              }
            });
            this.orderData = orderData;
          }
        })
        .catch(err => {
          //关闭加载提示
          this.$toast.clear();
        });
    },
    //确认收货
    receive(oid) {
      //订单编号
      //获取token
      let tokenString = localStorage.getItem("__TK");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/receive",
        data: {
          appkey: this.appkey,
          tokenString,
          oid
        }
      })
        .then(result => {
          //关闭加载提示
          this.$toast.clear();

          if (result.data.code == 80000) {
            this.orderData[oid].status = 2;
            //如果是待收货，则删除页面上的订单数据
            if (this.activeName == 1) {
              let index = this.orderNum.indexOf(oid);
              this.orderNum.splice(index, 1);
            }
          }
        })
        .catch(err => {
          //关闭加载提示
          this.$toast.clear();
        });
    },
    //删除订单
    removeOrder(oid) {
      //获取token
      let tokenString = localStorage.getItem("__TK");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      this.axios({
        method: "POST",
        url: "/removeOrder",
        data: {
          appkey: this.appkey,
          tokenString,
          oid
        }
      })
        .then(result => {
          //关闭加载提示
          this.$toast.clear();

          if (result.data.code == 90000) {
            //删除页面的订单
            let index = this.orderNum.indexOf(oid);
            this.orderNum.splice(index, 1);
          }
        })
        .catch(err => {
          //关闭加载提示
          this.$toast.clear();
        });
    }
  }
};
</script>
<style lang="less" scoped>
.order {
  padding-bottom: 50px;
  .order-item {
    padding: 10px;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0 0 2px 2px rgba(215, 180, 148, 0.4);
  }
  .pro-img {
    width: 60px;
    height: 60px;
    background-color: #ddd;
  }
  .pro-info {
    width: calc(~"100% - 70px");
    margin-left: 10px;
  }
  .names {
    height: 40px;
  }
  .zh-name {
    font-size: 14px;
  }
  .en-name {
    font-size: 12px;
    margin-top: 4px;
    color: #999;
  }
  .rule {
    height: 20px;
    font-size: 12px;
    color: #999;
    line-height: 20px;
  }
  .price {
    height: 40px;
    color: rgb(215, 180, 148);
    font-size: 16px;
    text-align: right;
  }
  div.price:nth-child(2) {
    color: brown;
    font-size: 20px;
  }
  .count {
    height: 20px;
    color: brown;
    font-size: 20px;
    line-height: 20px;
    text-align: right;
  }
  .items {
    margin-bottom: 10px;
  }
  .order-total {
    margin-top: 20px;
  }
  .total-count {
    font-size: 12px;
    color: #999;
    height: 40px;
    line-height: 40px;
  }
  .total-price {
    font-weight: bold;
    font-size: 16px;
    height: 40px;
    line-height: 40px;
    color: rgb(215, 180, 148);
  }
  /deep/ .van-submit-bar__price {
    color: #1989fa;
  }
  .popup-box {
    min-height: 300px;
    max-width: 580px;
    overflow-y: auto;
  }
  /deep/ .van-tab {
    transition: all 0.4s;
  }
  /deep/ .van-tab--active {
    font-size: 18px;
  }
  /deep/ .van-address-item .van-radio__icon--checked .van-icon {
    background-color: #1989fa;
    border-color: #1989fa;
    color: #fff;
  }
  /deep/ .van-tag--danger {
    background-color: #1989fa;
  }
  /deep/ .van-button--danger {
    background-color: #1989fa;
    border-color: #1989fa;
  }
  /deep/ .van-address-item__edit {
    display: none;
  }
  .order-content {
    padding: 10px;
  }
  .order-item-title {
    height: 20px;
    padding-bottom: 10px;
    margin-bottom: 10px;
    border-bottom: 1px solid #e8e8e8;
    line-height: 20px;
  }
  .order-no {
    color: #999;
    font-size: 12px;
  }
  .confirm {
    color: #666;
    font-size: 14px;
  }
  .delete {
    font-size: 18px;
    line-height: 20px;
    color: #666;
    margin-left: 10px;
    margin-top: -3px;
  }
  .delete-icon {
    position: relative;
    top: 4px;
  }
  .order-item {
    margin-bottom: 10px;
  }
}
</style>